.header__phone {
	color: #222;
}

@media screen and (max-width: 800px) {
	.header__phone {
		margin-left: auto;
		margin-right: 15px;
	}
}

@media screen and (max-width: 480px) {
	.header__phone {
		display: block;
		width: max-content;
		margin: 0;
		margin-left: auto;
		margin-bottom: 15px;
	}
}